<template>
<form action="" @submit.prevent="formSubmit">
    <div class="max-w-2xl mt-8 mx-auto p-4 bg-gray-200 rounded">
        <label for="" class="text-2xl">Role Assign</label>
        <div class="text-left p-2"> 
            <div class="text-left p-2 relative">
                <label for="">Find User</label>
                <Input v-if="!user" type="number" v-model="phone"  @input="onChange" class="w-full py-2 p-2 focus:outline-none" placeholder="Phone Number..." />
                 <Input v-else :value="user.name" v-model="form.user_id" class="w-full py-2 p-2 focus:outline-none"/>
            </div>
        </div>

        <div class="text-left p-2">
            <label for="">Role Name</label>
            <div class="grid grid-cols-3 gap-2">
                <div class="flex items-center" v-for="role in  roles" :key="role.id">
                    <input id="checkbox" type="checkbox" v-model="form.role" :value="role.id" class="w-4 h-4 text-blue-600 bg-gray-100 rounded border-gray-300 focus:ring-blue-500 dark:focus:ring-blue-600">
                    <label for="checkbox" class="ml-2 text-sm font-medium text-gray-900">{{ role.name }}</label>
                </div>
            </div>
        </div>

        <div>
            <Btn type="submit"> Submit </Btn>
        </div>
       
    </div>
</form>
    
</template>
<script>
import Select from "../../../components/select.vue";
import Btn from "../../../components/btn.vue";
import Input from "../../../components/input-new.vue";
export default {
    components:{
        Select,
        Btn,
        Input
    },
     data(){
        return {
            form : {
                role:[],
                user_id:''
            },
            phone: '',
        }

    },
    created(){
        this.$store.dispatch('roles/roleList');
    },
    computed:{
        roles(){
            return this.$store.state.roles.roles_list;
        },
        findUser(){
            return this.$store.state.adminAppointment.findUser || '';
        },
        user(){
            const user = this.findUser.user || '';
            if(user){
                this.form.user_id = user.id;
                return user;
            }

        },
    },
    methods:{
        formSubmit(){
           this.$store.dispatch('roleAssign/dataSubmit',{
                data: this.form,
           }).then( () => {
                this.$router.replace({
                    path:'/admin/role-assign'
                })
           });
        },
        onChange(){
            if(this.phone.length > 10){
              this.$store.dispatch('adminAppointment/findUser',{
                    phone: this.phone,
                })
            }
        },
    }
}
</script>